<template>
    <div>
        <el-card shadow='never'>
            <div slot="header" class="clearfix">
                <span>优惠券发放</span>

                <!--    <el-button style="float:right" type="primary" size='small' @click='add_banner'>新增</el-button> -->
            </div>
            <el-form   :model="dynamicValidateForm" ref="dynamicValidateForm" :rules="rule" label-width="180px" class="demo-dynamic coupon_list">
                <el-form-item label="优惠券名称(活动)"  prop="activity_id" >
                    <el-select  placeholder="请选择"  v-model="dynamicValidateForm.activity_id" @change="couponSelect">
                        <el-option v-for='(item,index) in tableData' :key='index' :label="item.Name" :value="item.ID"></el-option>
                    </el-select>
                </el-form-item>
                <router-link v-if="dynamicValidateForm.activity_id" :to="{path:'/coupon/send_batch',query:{activity_id:dynamicValidateForm.activity_id}}" style="position:absolute;top:0;left:483px">
                    <el-button>批量发券</el-button>
                </router-link>
                <el-form-item v-for="(domain, index) in dynamicValidateForm.domains" :label="'发送账户(用户名)' + index" :key="domain.key" :prop="'domains.' + index + '.value'" :rules="{
                    required: true, validator:validateAccount, trigger: 'blur'
                }">
                    <el-input v-model="domain.value" style="width:300px"></el-input>
                    <el-button v-if='index>0' @click.prevent="removeDomain(domain)">删除</el-button>
                    <span  class='info-tag' v-if='domain.code'>{{domain.code == 'success'?"成功":"失败"}}</span>
                    <el-button @click="addDomain" v-if='index+1 == dynamicValidateForm.domains.length'>新增账户</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="submitForm('dynamicValidateForm')">提交</el-button>

                    <el-button @click="resetForm('dynamicValidateForm')">重置</el-button>
                </el-form-item>
            </el-form>

        </el-card>

    </div>
</template>

<script>
    import { mapState } from "vuex";
    export default {
        name: "banner",
        data() {
            var validateAccount = (rule, value, callback) => {
                if (value == ""||!value) {
                    callback("账户不能为空!");
                }else {
                    this.$api("/admin/user/account_exist", {
                        account:value,
                        errorHandle:true,
                    }).then(res => {
                        if(res.code !=0){
                        callback(res.msg);
                    }else{
                        callback();
                    }


                });
                }
            };
            return {
                validateAccount:validateAccount,
                dynamicValidateForm: {
                    activity_id:'',
                    domains: [
                        {
                            value: ""
                        }
                    ]
                },
                rule:{
                    activity_id:[{ required: true, message: '活动不能为空', trigger: 'blur'}]
                },
                tableData:[],
                status:-1
            };
        },
        computed: {},
        methods: {
            couponSelect(val){
                let sendEndTime = ''
                let sendName = ''
                for(var i =0,len=this.tableData.length;i<len;i++){
                    if(this.dynamicValidateForm.activity_id == this.tableData[i].ID){
                        sendEndTime = this.tableData[i].EndTime
                        sendName = this.tableData[i].Name
                        break;
                    }
                }
                sessionStorage.setItem('sendEndTime', JSON.stringify(sendEndTime))
                sessionStorage.setItem('sendName', JSON.stringify(sendName))
            },
            submitForm(formName) {
                this.$refs[formName].validate(valid => {
                    if (valid) {
                        let account = [];
                        let expire = '';
                        for(var i=0,len=this.dynamicValidateForm.domains.length;i<len;i++){
                            account.push(this.dynamicValidateForm.domains[i].value);
                        }
                        for(var i =0,len=this.tableData.length;i<len;i++){
                            if(this.dynamicValidateForm.activity_id == this.tableData[i].ID){
                                expire = this.tableData[i].EndTime
                                break;
                            }
                        }
                        this.$api("/admin/coupon/new_coupon_record_add", {
                            activity_id:this.dynamicValidateForm.activity_id,
                            expire:expire,
                            accounts:account.join(',')
                        }).then(res => {
                            if(res.code == 0){
                            for(var i=0,len=this.dynamicValidateForm.domains.length;i<len;i++){
                                if(res.data[i]){
                                    this.$set(this.dynamicValidateForm.domains[i],"code","success")
                                }else{
                                    this.$set(this.dynamicValidateForm.domains[i],"code","fail")
                                }
                            }
                        }
                    });
                    } else {
                        console.log("error submit!!");
                return false;
            }
            });
            },
            getList() {
                let params = {
                    status: this.status,
                    pageNo: 0,
                    pageSize: 100
                };
                this.$api("/admin/coupon/activity_list", params).then(res => {
                    this.tableData = res.data.list;

            });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
                this.dynamicValidateForm = {
                    activity_id:'',
                    domains: [
                        {
                            value: ""
                        }
                    ]
                }
            },
            removeDomain(item) {
                var index = this.dynamicValidateForm.domains.indexOf(item);
                if (index !== -1) {
                    this.dynamicValidateForm.domains.splice(index, 1);
                }
            },
            addDomain() {
                this.dynamicValidateForm.domains.push({
                    value: "",
                    key: Date.now()
                });
            }
        },
        created() {
            this.getList();
            // this.getFacilityDetail(16)
        }
    };
</script>


<style lang='scss' scoped>
    .card {
        width: 300px;
        height: 200px;
        float: left;
        margin: 20px;
    img {
        width: 100%;
        display: block;
        height: 140px;
    }
    }
    .info-tag{
        color: red;
        margin:0 10px;
        font-weight: bold;

    }
    .coupon_list {
        width: 680px;
        position: relative;
    .item {
        padding: 10px 0;
        width: 400px;
    }
    }
</style>



